.skills {
  padding: 2rem;
  background-color: #f9f9f9;
}

.skills h2 {
  text-align: center;
  font-size: 2rem;
  margin-bottom: 2rem;
  color: #333;
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1.5rem;
}

.skill-card {
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  padding: 1.5rem;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.skill-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

.skill-card h3 {
  font-size: 1.5rem;
  color: #0066cc;
  margin-bottom: 1rem;
}

.skill-card ul {
  list-style: none;
  padding-left: 0;
}

.skill-card ul li {
  margin: 0.5rem 0;
  font-size: 1rem;
  color: #555;
  position: relative;
  padding-left: 1.5rem;
}

.skill-card ul li::before {
  content: "✔";
  color: #0066cc;
  position: absolute;
  left: 0;
  top: 0;
}
